import "./node_modules/bootstrap/dist/css/bootstrap.css"
import "./node_modules/@fortawesome/fontawesome-free/css/all.css"
import "./src/styles/global.css"

import $ from "jQuery"
import Typed from "typed.js/src/typed";

// or:
// require('./src/styles/global.css')

const addScript = url => {
    const script = document.createElement("script")
    script.src = url
    script.async = true
    document.body.appendChild(script)
}

export const onClientEntry = () => {
     window.onload = () => {


         $(document).ready(function () {

             $(window).on('scroll', function () {
                 if ($(window).scrollTop() > 100) {
                     $('#mainNav').addClass("navbar-shrink");
                 } else {
                     $('#mainNav').removeClass("navbar-shrink");
                 }
             });

         });

         // Add class to Navbar when page is scrolled
         $(window).on('activate.bs.scrollspy', function () {
             console.log('active.bs.scrollspy');
         });


         {/* <!--Animate scrolling in navigation bar --> */}
         $('a[href^="#"][href!="#experience"][href!="#education"][href!="#service"]').on('click', function (e) {
             e.preventDefault();

             var target = this.hash,
                 $target = $(target);

             $('html, body').stop().animate({
                 'scrollTop': $target.offset().top
             }, 900, 'swing', function () {
                 window.location.hash = target;
             });
         });

//         addScript("/mytypewriter.js")
//         addScript("/navshrink.js")
//         addScript("/navScroll.js")
//         addScript("/animateScroll.js")
     }
}

